import React from 'react';
import styles from './QnaBbsTableMobile.scss';
import classNames from 'classnames/bind';
import { Link } from 'react-router-dom';
import FaLongIcon from 'react-icons/lib/fa/long-arrow-right';
const cx = classNames.bind(styles);

const QnaBbsTableMobile = ({list}) => {
  const qnaList = list.map(
    (qna, i) => {
      return (
        <Link className={cx('row')} to={`/qna/detail/${qna.get('id')}`} key={i}>
          <div className={cx('title')}>
          <FaLongIcon style={{display: parseInt(qna.get('depth'), 10) > 1 ? "inline-block" : "none" }}/>
          {
            parseInt(qna.get('depth'), 10) > 1 ? 
            
            `: ${qna.get('title')}`
            :
            `${qna.get('title')}`
          }
          </div>
          <div className={cx('bottoms')}>
            <div className={cx('username')}>
            {qna.get('username')}
            </div>
            <div className={cx('date')}>
            {qna.get('createdAt')}
            </div>
          </div>
        </Link>
      )
    }
  )
  return (
    <div className={cx('table')}>
    {
      list.size === 0 ?
      <div className={cx('empty-table')}>
        <div className={cx('desc')}>
          <div className={cx('text')}>
                .
          </div>
        </div>
      </div>
      :
      <div className={cx('contents')}>
        {
          qnaList
        }
      </div>
    }
      
    </div>
  );
}

export default QnaBbsTableMobile;


